@import url(https://fonts.googleapis.com/css?family=Cagliostro);
@import url(https://fonts.googleapis.com/css?family=Doppio+One);
@import url(https://fonts.googleapis.com/css?family=Questrial);
@import url(https://fonts.googleapis.com/css?family=Hind);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,body{width:100%;height:100vh;margin:0 auto;overflow:hidden}.App{min-height:100vh;display:flex;flex-direction:column;align-items:center;margin:0 auto}.App #svg{min-width:100vh;min-height:100vh;position:absolute;opacity:0.8;z-index:-2}.App #svg #cloud1{animation:move1 35s linear infinite}.App #svg #cloud2{animation:move2 45s linear infinite}.App #svg #cloud3{animation:move3 55s linear infinite}@keyframes move1{0%{transform:translate(0px, 25.36px)}100%{transform:translate(-570px, 25.36px)}}@keyframes move2{0%{transform:translate(0px, 25.36px)}100%{transform:translate(-700px, 25.36px)}}@keyframes move3{0%{transform:translate(0px, 25.36px)}100%{transform:translate(-800px, 25.36px)}}header{width:100%;background-color:#e0e0e0;margin:0 auto;display:flex;flex-direction:row;align-items:center;position:relative;z-index:1}header .logo{width:80%;display:flex;flex-direction:row;margin:0 auto;padding:0}header h1{margin:0;padding:0.7em 0 0.5em 0;font-family:"Cagliostro", sans-serif;text-align:left;font-size:2.5em;letter-spacing:3px;color:rgba(243,79,79,0.83);text-shadow:#000 2px 2px 2px}header img{margin-right:-1.3em;padding:0.7em 0 0.5em 7em;width:105px;height:59.5px}header .date{width:40%;margin:0;font-family:"Hind",sans-serif;font-size:1.7em;letter-spacing:2px;color:#0a031e}main{flex-grow:1;text-align:center;display:flex;justify-content:center;margin:0 auto}main .main-container{display:flex;flex-direction:column;margin:0 auto;position:relative}main .main-container .search-section{padding-top:40px;display:flex;flex-direction:column}main .main-container .search-section span{font-family:"Hind",sans-serif;font-size:1.5em}main .main-container .search-section .current-location{padding-top:30px}main .main-container .search-section .current-location .target{color:#b63535;cursor:pointer;outline:none;vertical-align:middle;width:40px;height:40px}main .main-container .search-section .search-coordinates .search{width:150px;height:30px;margin:5px;padding:5px;border:none;text-align:center;color:#0a031e;background-color:rgba(255,251,248,0.3);border-bottom:1px #0a031e solid}main .main-container .search-section .search-coordinates .search-button{color:#b63535;cursor:pointer;outline:none;vertical-align:middle;width:40px;height:40px;padding-left:5px}main .main-container .weather-section{display:flex;justify-content:center;text-align:center;margin-top:40px;padding-top:10px;background-color:rgba(63,63,63,0.2);box-shadow:rgba(0,0,0,0.2) 0px 4px 12px}main .main-container .weather-section .weather{text-align:center;margin:4rem 0}main .main-container .weather-section .weather .location{display:flex;flex-direction:column;justify-content:center}main .main-container .weather-section .weather .location h2{font-family:"Doppio One", sans-serif;text-transform:uppercase;text-align:center;font-size:3em;letter-spacing:2px;color:#0a031e;margin:0 auto}main .main-container .weather-section .weather .location h3{font-family:"Doppio One", sans-serif;text-transform:uppercase;text-align:center;font-size:2em;letter-spacing:1px;color:#0a031e;margin:0 auto}main .main-container .weather-section .weather .options .temp span{font-size:6em}main .main-container .weather-section .weather .options .temp .c{font-size:3em}main .main-container .weather-section .weather .options .wi-hu{border-top:1px solid #0a031e;display:flex;justify-content:center}main .main-container .weather-section .weather .options .wi-hu .windspeed{padding-top:20px;padding-right:15px}main .main-container .weather-section .weather .options .wi-hu .humidity{padding-top:20px}main .main-container .weather-section .weather .options .wi-hu span{font-family:"Hind",sans-serif;font-size:2em}main .main-container .weather-section .weather .options>*{margin:0 0.3rem}main .main-container .weather-section .weather .react-icons{color:#b63535;vertical-align:middle;width:60px;height:60px;margin-top:-15px}footer{width:100%;text-align:center;background-color:#e0e0e0;font-family:"Helvetica";font-size:1vh;letter-spacing:3px;color:#0a031e}@media only screen and (max-width: 600px){html,body{overflow:auto;background:url(/diggweather/static/media/background.fcf4072c.svg);background-repeat:no-repeat;background-size:cover}#svg{display:none}header{flex-direction:column}header .logo{width:100%}header h1{margin:0;padding-top:20px;font-family:"Cagliostro", sans-serif;text-align:left;font-size:1.1em;letter-spacing:3px;color:rgba(243,79,79,0.83);text-shadow:#000 1px 1px 1px}header img{margin-right:-1.3em;padding:5px 0 0 5px;width:85px;height:59.5px}header .date{text-align:center;padding-bottom:10px;width:100%;margin:0;font-family:"Hind",sans-serif;font-size:1em;letter-spacing:2px;color:#0a031e}main .main-container{padding:10px;background-color:rgba(63,63,63,0.1);box-shadow:rgba(0,0,0,0.2) 0px 4px 12px}main .main-container .search-section{padding-top:15px;display:flex}main .main-container .search-section span{font-size:1em}main .main-container .search-section .current-location{padding-top:20px}main .main-container .search-section .search-coordinates span{display:none}main .main-container .search-section .search-coordinates .search{width:100px;height:30px;margin:1px;padding:1px;border:none;text-align:center;color:#0a031e;background-color:rgba(255,251,248,0.3);border-bottom:1px #0a031e solid}main .main-container .weather-section{margin-top:10px;padding-top:0}main .main-container .weather-section .weather{margin:3em 0}main .main-container .weather-section .weather .location{padding:5px}main .main-container .weather-section .weather .location h2{font-size:2em}main .main-container .weather-section .weather .location h3{font-size:1em}main .main-container .weather-section .weather .options .wi-hu span{font-size:1.5em}main .main-container .weather-section .weather .options .wi-hu .react-icons{width:40px;height:40px;margin-top:-5px}}

